p {
    margin: 5px;
}

a {
    text-decoration: none;
}

.capitalize {
    text-transform: capitalize;
}

.flex {
    display: flex;
}

.inline {
    display: inline;
}

.cta {
    color: #f50057 !important;
}

.header-appbar {
    justify-content: space-between;
    .brand-logo {
        height: 70px;
        margin: 10px;
    }
    .app-header {
        font-size: 1.5em;
        display: block;
    }
}

.blocks-container {
    padding: 10px 50px;
    .block-heading {
        text-transform: capitalize;
        text-align: center;
        margin-top: 50px;
    }
    .block-content {
        text-align: center;
        font-size: 1.2em;
        font-weight: 300;
        margin: 30px 0;
    }
}

.textField {
    width: 200;
}

.title {
    flex-grow: 1;
}

.content-container {
    position: absolute;
    height: fit-content;
    text-align: center;
    min-height: 100%;
    top: 0;
    .banner-image-content {
        padding: 24px;
        position: relative;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        .banner-heading {
            font-size: 3em;
            font-weight: 400;
            color: white;
            margin: 10px;
        }
        .banner-subheading {
            font-size: 1.7em;
            font-weight: 200;
            color: white;
            margin: 0 0 20px;
        }
    }
}

.bannerImageContainer {
    background-image: url('/assets/images/9stacks-banner.png');
    position: relative;
    box-shadow: none;
    background-size: contain;
    background-repeat: no-repeat;
    .bannerImg {
        width: 100%;
        visibility: hidden;
    }
}

.input-container {
    background: white;
    display: flex;
    align-items: center;
    width: fit-content;
    padding: 1px 10px;
    border-radius: 5px;
    min-width: 400px;
    justify-content: space-between;
    .contact-label {
        font-size: 1.9em;
        padding: 10px 5px;
    }
    .input-box {
        border: none;
        outline: none;
        padding: 10px;
        min-width: 250px;
        font-size: 2em;
    }
    .btn {
        padding: 10px 20px;
    }
}

.card-icons {
    background-size: contain !important;
    height: 120px;
}

.iframe-container {
    max-width: 550px;
    width: 100%;
}

.grid-parent {
    display: flex;
    flex-direction: row;
    margin: 0 -10px;
    .grid-container {
        display: flex;
        flex-direction: column;
        text-align: center;
        box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
        padding: 15px;
        border: 1px solid #efefef;
        margin: 10px;
        width: 100%;
        border-radius: 2px;
    }
    .title {
        font-size: 1.5em;
        font-weight: 400;
    }
    .subtitle {
        font-size: 1.2em;
        font-weight: 300;
    }
}

.footer-container {
    background: #efefef;
    padding: 50px;
    margin: 40px 0;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-around;
    .label-section {
        font-size: 1.8em;
        margin-bottom: 0;
    }
}

@media only screen and (max-width: 600px) {
    .header-appbar {
        .brand-logo {
            margin: 5px;
        }
        .app-header {
            display: none;
        }
    }
    .content-container {
        position: relative;
        .banner-image-content {
            padding: 0;
            .banner-heading {
                font-size: 1.5em;
                color: #222;
                margin: 2px;
            }
            .banner-subheading {
                font-size: 1em;
                color: #222;
                margin: 0 0 10px;
            }
        }
    }
    .bannerImageContainer {
        background-image: none;
        overflow: hidden;
        .bannerImg {
            height: 225px;
            width: inherit;
            visibility: visible;
        }
    }
    .blocks-container {
        padding: 15px;
    }
    .grid-parent {
        flex-direction: column;
        margin: 0;
        .grid-container {
            margin: 10px 0;
        }
    }
    .footer-container {
        padding: 15px;
        margin: 20px 0;
        flex-direction: column;
        .label-section {
            font-size: 1.5em;
            margin-bottom: 10px;
        }
    }
    .input-container {
        background: white;
        border: 1px solid #e2e2e2;
        display: flex;
        align-items: center;
        width: -moz-fit-content;
        width: fit-content;
        padding: 1px 10px;
        border-radius: 5px;
        min-width: 300px;
        justify-content: space-between;
        .contact-label {
            font-size: 1em;
            padding: 10px 5px;
        }
        .input-box {
            padding: 10px;
            min-width: 150px;
            font-size: 1em;
        }
        .btn {
            padding: 3px 10px;
        }
    }
    .block-heading {
        margin-top: 25px;
    }
}


/* Transitions Effects */

.fadein-appear {
    opacity: 0.01;
}

.fadein-appear.fadein-appear-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
}